.navbar {
  background: #40685f;
  flex-direction: row;
  justify-content: stretch;
  align-items: stretch;
  width: 100%;
  height: 60px;
  display: flex;
  position: static;
}

.nav-but {
  float: left;
  text-transform: uppercase;
  background: none;
  border: none;
  height: 60px;
  padding: 6px 12px;
  font-family: Montserrat, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: bold;
  text-decoration: none;
  display: block;
}

.nav-but:hover {
  background: #203833;
  border: 1px solid #4c746c;
  border-radius: 10% / 50%;
  box-shadow: 1px 2px 5px #000;
}

.nav-but:active {
  background: #203833;
  border: 1px solid #4c746c;
  border-radius: 10% / 50%;
  box-shadow: inset 1px 2px 5px #000;
}

#icoli {
