}

footer {
  grid-template-columns: 1, 1, 1;
  width: 100%;
  height: 100px;
  display: grid;
  position: sticky;
}

.foot {
  height: inherit;
  grid-area: 1 / 2;
  width: 33%;
}

.ft {
  background-color: #40685f;
  border: none;
  border-radius: 50%;
  width: 17vw;
  height: 18vw;
  margin-bottom: 1vw;
  translate: 200%;
  box-shadow: 5px 5px 1px #000, 10px 10px 10px;
}

.ft:hover {
  color: #ecf1f0;
  background-color: #4c746c;
  animation: 1s linear luz;
  box-shadow: 1px 2px 5px #000;
}

.ft:active {
  background: #203833;
  box-shadow: inset 1px 2px 5px #000;
}

#gf2 {
  float: right;
  height: inherit;
  grid-area: 1 / 1;
  width: 77%;
  display: block;
}

#up {
  width: 100%;
  height: 100%;
}

